export default {

  typicalRequest(reqP, cb) {
    return reqP
      .catch((err) => {
        window.location.pathname = '/login';
      });
  },

  getVendors(cb) {
    return this.typicalRequest($.get('/vendors'));
  },

  getEvents(cb) {
    return this.typicalRequest($.get('/events'));
  }

};



